x;\n\n  &::after {\n    bottom: unset !important;\n    top: 0 !important;\n  }\n\n  &Primary {\n    &::after {\n      opacity: 11%;\n      @include styleGradientPseudoEl;\n      @include linear-gradient-green(90deg);\n      right: 0;\n    }\n  }\n\n  &Danger {\n    &::after {\n      opacity: 11%;\n      @include styleGradientPseudoEl;\n      @include linear-gradient-danger(90deg);\n      right: 0;\n    }\n  }\n\n  &Purple {\n    &::after {\n      opacity: 11%;\n      @include styleGradientPseudoEl;\n      @include linear-gradient-purple(90deg);\n      right: 0;\n    }\n  }\n\n  &Green {\n    &::after {\n      opacity: 11%;\n      @include styleGradientPseudoEl;\n      @include linear-gradient-green(90deg);\n      right: 0;\n    }\n  }\n\n  &Title {\n    min-height: 50px;\n    font-size: 18px;\n    display: flex;\n    align-items: center;\n    position: relative;\n    margin: 0px;\n    padding: 15px;\n    cursor: pointer;\n\n    &Primary {\n      &::before {\n        opacity: 14%;\n        @include styleGradientPseudoEl;\n        @include linear-gradient-grey(-90deg);\n        left: 0;\n      }\n    }\n\n    &Danger {\n      &::before {\n        opacity: 11%;\n        @include styleGradientPseudoEl;\n        @include linear-gradient-danger(-90deg);\n        left: 0;\n      }\n    }\n\n    &Green {\n      &::before {\n        opacity: 11%;\n        @include styleGradientPseudoEl;\n        @include linear-gradient-green(-90deg);\n        left: 0;\n      }\n    }\n\n    &Content {\n      display: flex;\n      align-items: center;\n      min-height: 32px;\n      line-height: 25px;\n      width: 100%;\n      transition: 1.5s ease-in-out;\n      opacity: 0;\n\n      &entering {\n        opacity: 0;\n        transition: 1ms ease-in-out;\n      }\n\n      &entered {\n        opacity: 1;\n      }\n\n      &exiting {\n        opacity: 0;\n        transition: 1ms ease-in-out;\n      }\n\n      &exited {\n        opacity: 1;\n      }\n    }\n  }\n\n  &Content {\n    position: re